@import 'src/bootstrap';
@import 'extend/mixins';
@import 'extend/application';

* {
	margin:0;
	padding:0;
}
*:focus {
	outline: none;
}
html, body {
	height: 100%;
}

html {
	overflow-y:scroll;
}

body {
	font-family: "Arial", Gadget, sans-serif;
	color: #000;
	margin: 0;
	padding: 0;
}

#admin-window {
	min-width: 960px;
	background: url('../images/bg_white_hatch.jpg');
}

#body-wrapper {
	clear: both;
	color: #000000;
	padding: 0 20px;
	
	margin:0 auto -90px;
	min-height:100%;
	height:100%; 

	max-width: 1850px;
	min-width: 940px;
	float: none;

	p {
		color: #000000;
	}
}

#wrap, .container-full  {
	max-width: 1850px;
	min-width: 940px;
	margin: 0 auto;
	float: none;
}

#wrap {
	float: left;
	width: 100%;
	padding-top: 40px;
    padding-bottom: 40px;
	position: relative;

	p {
		color: black;
	}
	
	h1.title { margin-bottom: 20px; }
}

table.manage_content {

  .label {
    padding: 5px;
//    font-size: 75%;
  }

  td:last-child {
    width: 150px;
    text-align: right;
  }
}

.page-details {
	display: block;
	padding-top: 0px;
	padding-bottom: 10px;
	height: 30px;
	clear: both;
	float: none;
	width: 100%;
}

#wrap .filter-links {
	padding-top: 10px;
	float: left;
	display: block;
	position: relative;

	a {
		&:link, 
		&:visited {
			font-size: 14px;
			color: #9E9E9E;
			text-decoration: none;
		}
	}
}
.title-search {
	float: right;
	width: 300px;
}

.search-text,
.search-button {
	float: left;
}

.search-text {
	width: 190px;
}

.search-button {
	width: 110px;
}

.title-search {
	input {
		float: right;
		margin: 5px 0 0;
	}

	input[type="button"] {
		margin: 0;
	}
}


/*
* Editor
* ------------------------------------------------------------------------------------------- */

.editor { 
	width:98%;
	min-height: 550px;
}

.wysiwyg { margin-top:15px;}

/* See codemirror.less */

/*
* Tabs 
* ------------------------------------------------------------------------------------------- */
.tabs:before, .tabs:after {
	display: table;
	content: ""; 
}

.tabs:after {
	clear: both;
}
#wrap {
	ul.nav-tabs {
		list-style: none;
		margin: 20px 0 0 0;
		padding: 0;
		list-style: none;
		zoom: 1;
		border: 0;

		li {
			display: inline;
			margin-bottom: 0px;
			float: left;
		}

		li a {
			background-color: #CCCCCC;
		    border-radius: 7px 7px 0 0;
		    color: #000000;
		    font-size: 18px;
		    padding: 10px;
		    text-decoration: none;
			display: block;
		}

		li a.active,
		li a:hover {
			background-color: #333;
			color: #fff;
		}

		li a:focus {
			outline: 0;
		}
	}
}
.tab-body {
	background-color: #F5F5F5;
	padding: 20px;
	border-radius: 0px 7px 0 0;
	border: 1px solid #CCCCCC;
}

/*
Topbar
* ------------------------------------------------------------------------------------------- */
.navbar{ 

	p {
		padding-top: 15px;
		margin: 0;
	}
}

/*
Admin Sidebar
* ------------------------------------------------------------------------------------------- */
.has-right-sidebar {
	.has-tabs {
		padding-top: 70px;
	}
	
	.contet-sidebar {
		display: block;
		margin-top: 50px;
		margin-bottom: 20px;
		clear: right;
		float: right;
		position: relative;
		width: 281px;
	}

	.contet-sidebar .table {
		margin-bottom: 20px;
	}

	#post-body {
		clear: left;
		float: left;
		margin-right: -340px;
		width: 100%;
	}

	#post-body-content {
		margin-right: 310px;
	}
}

.full-content {
	#post-body {
		clear: left;
		float: left;
		width: 100%;
	}

	#post-body-content {
		padding-left: 20px;
	}
}

.title {
	h1 {
		margin-bottom: 20px;
		text-shadow: 0 1px 0 #FFFFFF;
	}

	.button {
		margin-bottom: 0px;
		top: 15px;
		margin-left: 10px;
	}

	a.button {
		line-height: 55px;
	}

	.title-search {
		padding-top: 15px;
		width: 300px;
		margin-top: 10px;
	}
}

/* 
Table
* ------------------------------------------------------------------------------------------- */
.table {
	padding-top: 10px;
	margin-right: 30px;
	
	a:link {
		text-decoration: none;
	}

	fieldset {
		padding: 0 0 20px 0;
	}
	.table-content fieldset dt {
		margin-bottom: 0px;
	}

	.table-content select {
		width: 100%;
	}
}

#post-body-content {
  .table .table-content {
    margin-bottom: 1.5em;
  }

  .content_title {
    font-size: 18px;
  }
}

#wrap .pagination {
	
	margin: 0;
	padding: 0;
	
	ul {
		border: 0;
	}
	
	a:link,
	a:visited {
		line-height: 20px;
		padding: 3px 6px;
		background-color: white;
		border: 1px solid #CCCCCC;
		margin-left: 5px;
		text-decoration: none;
		border-radius: 4px;
		-moz-border-radius: 4px;
		-webkit-border-radius: 4px;
	}
	a:hover {
		background:	none repeat scroll 0 0 #0069D6;
		color: #fff;
	}
	em {
		line-height: 30px;
		margin-left: 10px;
	}
}

#body-wrapper table * {
	vertical-align: middle !important;
}

table {
	margin-top: 20px;
	&.nostyle {
		border: none;
		background: none;	
		margin: 0;
	}
	
	&.nostyle tbody tr.even td,
	&.nostyle tbody tr td {
		background: none;	
	}
	&.nostyle td {
		padding: 0px;	
	}
}

.table-heading, thead {
	height: 45px;
	border: 1px solid #CCCCCC;
	-webkit-border-top-left-radius: 7px;
	-webkit-border-top-right-radius: 7px;
	-moz-border-radius-topleft: 7px;
	-moz-border-radius-topright: 7px;
	border-top-left-radius: 7px;
	border-top-right-radius: 7px;
	background: #EEEEEE;

	background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #EEEEEE), color-stop(100%, #D8D8D8));
	background: -webkit-linear-gradient(top, #EEEEEE 0%, #D8D8D8 100%);
	background: -moz-linear-gradient(top, #EEEEEE 0%, #D8D8D8 100%);
	background: -o-linear-gradient(top, #EEEEEE 0%, #D8D8D8 100%);
	background: -ms-linear-gradient(top, #EEEEEE 0%, #D8D8D8 100%);
	background: linear-gradient(top, #EEEEEE 0%, #D8D8D8 100%);

	text-shadow: 0 1px 0 #FFFFFF;
}

.table-heading {
	h3 {
		padding: 10px 0 10px 10px;
		margin: 0;
		color: #000000;
		float: left;
		font-size: 1.55em;
	}
	h3+input.btn {
		margin: 8px 10px 0 0;
	}
}

.table-content {
	padding: 9px;
	border-left: 1px solid #CCCCCC;
	border-right: 1px solid #CCCCCC;
	border-bottom: 1px solid #CCCCCC;
	
	> .form-actions {
		margin-bottom: 0;
	}
	
	fieldset {
		padding: 0;
	}
}

.error {
	color: red;
}

input[type="text"].error, 
input[type="text"].error:focus {
	border: 1px solid red;
}


/*
Category, Post-type List
* ------------------------------------------------------------------------------------------- */
.category-list, .post-type-list {
	padding: 5px;
	background: #f8f8f8;
	overflow: auto;
	height: 150px;
	
	ul{
		margin: 0;
	}
	li{
		list-style: none;
	}
}

/* Tool Tips 
* ------------------------------------------------------------------------------------------- */
.tooltip {
	position: absolute;
	width: 250px;
	background-image: url('http://papermashup.com/demos/tooltip/tip-bg.png');
	background-position: left center;
	color: #FFF;
	padding: 5px 5px 5px 18px;
	font-size: 12px;
	font-family: Verdana, Geneva, sans-serif;
}
	
.tooltip-image {
	float: left;
	margin-right: 5px;
	margin-bottom: 5px;
	margin-top: 3px;
}
	
	
.tooltip span {
	font-weight: 700;
	color: #ffea00;
}


/* 
Tags
* ------------------------------------------------------------------------------------------- */
div.tagsinput {
	border:1px solid #CCC;
	background: #FFF;
	padding: 3px;
	width:300px;
	height:100px;
	overflow-y: auto;
	border-radius: 3px;

	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	border-radius: 3px;


	span {
		&.tag {
			-moz-border-radius:2px;
			-webkit-border-radius:2px;
			display: block;
			float: left;
			padding: 5px;
			text-decoration:none;
			background: #3879d9;
			color: #fff;
			margin-right: 3px;
			margin-bottom:3px;
			font-family: Helvetica;
			font-size:12px;
		}

		&.tag a {
			font-weight: bold;
			color: #fff;
			text-decoration:none;
			font-size: 11px;
		}
	}

	input {
		width:80px;
		margin:0px;
		font-family: helvetica;
		font-size: 11px;
		border:0px solid transparent;
		padding:5px;
		background: transparent;
		color: #000;
		outline:0px;
		margin-right:5px;
		margin-bottom:5px;
	}
	
	input,textarea{
			-webkit-box-shadow: none;
			-moz-box-shadow: none;
			box-shadow:inset none;
		}

	div {
		display:block;
		float: left;
	}
}

.tags_clear {
	clear: both;
	width: 100%;
	height: 0px;
}


