  /*	--------------------------------------------------
      Reveal Modals
      -------------------------------------------------- */
  .reveal-modal-bg {
    position: fixed;
    height: 100%;
    width: 100%;
    background: #000;
    background: rgba(0,0,0,.8);
    z-index: 100;
    display: none;
    top: 0;
    left: 0;
  }

  .reveal-modal {
    visibility: hidden;
    top: 100px;
    left: 50%;
    margin-left: -300px;
    width: 520px;
    background: #eee;
    position: absolute;
    z-index: 101;
    padding: 30px 40px 34px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-box-shadow: 0 0 10px rgba(0,0,0,.4);
    -webkit-box-shadow: 0 0 10px rgba(0,0,0,.4);
    -box-shadow: 0 0 10px rgba(0,0,0,.4);

    &.small 		{ width: 200px; margin-left: -140px;}
    &.medium 		{ width: 400px; margin-left: -240px;}
    &.large 		{ width: 600px; margin-left: -340px;}
    &.xlarge 		{ width: 800px; margin-left: -440px;}

    .close-reveal-modal {
      font-size: 22px;
      line-height: .5;
      position: absolute;
      top: 8px;
      right: 11px;
      color: #aaa;
      text-shadow: 0 -1px 1px rbga(0,0,0,.6);
      font-weight: bold;
      cursor: pointer;
    }
  }


  /*
  Media Upload
  * ------------------------------------------------------------------------------------------- */

  /*-------------------------
      Image Previews
  --------------------------*/
  #dropbox {

    &.well {
      font-size: 150px;
      color: #ccc;
      text-align: center;
      height: 200px;
      margin-bottom: 30px;
      display: block;
    }

    .preview{
      width:245px;
      height: 215px;
      margin: 20px 20px 0 20px;
      position: relative;
      text-align: center;
      float: left;

      img{
        max-width: 240px;
        max-height:180px;
        border:3px solid #fff;
        display: block;
      }

      &.done .uploaded{
        display: block;
      }
    }

    .imageHolder{
      display: inline-block;
      position:relative;
    }

    .uploaded{
      position: absolute;
      top:0;
      left:0;
      height:100%;
      width:100%;
      background: url('../images/done.png') no-repeat center center rgba(255,255,255,0.5);
      display: none;
    }

    /*-------------------------
        Progress Bars
    --------------------------*/
    .progressHolder {
      position: absolute;
      background-color:#252f38;
      height:12px;
      width:100%;
      left:0;
      bottom: 0;
      box-shadow:0 0 2px #000;
    }

    .progress_bar {
      background-color:#2586d0;
      position: absolute;
      height:100%;
      left:0;
      width:0;
      box-shadow: 0 0 1px rgba(255, 255, 255, 0.4) inset;
      -moz-transition:0.25s;
      -webkit-transition:0.25s;
      -o-transition:0.25s;
      transition:0.25s;

      &.done, &{
        width:100% !important;
      }
    }
  }

